<template>
  <div>
    <div v-if="pageLoading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <div id="question1" v-else>
      <b-button variant="primary" class="px-16 lable-btnAny">{{
        $t("QUESTION") + questions.question_no
      }}</b-button>
      <br /><br />
      <div class="title-fw title-fs m-left-3">
        {{ questions.question_contents }}
      </div>
      <br /><br />
      <b-row>
        <b-col xl="2"></b-col>
        <b-col xl="6">
          <span class="d-flex flex-wrap w-100 p-2">
            <template v-if="questions.question_kbn == '01'">
              <b-form-radio-group
                id="judgement-group-expense"
                v-model="sessionAnswers"
                :options="answerOptions"
                name="judgement-group-expense"
                class="normal-fs pt-1 highLightHeaderLabel"
                size="sm"
              ></b-form-radio-group>
            </template>
            <template v-else>
              <b-form-select
                v-model="sessionAnswers"
                :plain="true"
                :options="answerOptions"
                class="w-50 optionClass"
                size="md"
              ></b-form-select>
              <br />
            </template>
          </span>
          <div class="errorPadding">
            <show-errors
              :control="$v.sessionAnswers"
              :field="$t('SELECTANSWER')"
            />
          </div>
        </b-col>
      </b-row>
      <br />
      <br />
      <b-row class="justify-content-center">
        <b-col cols="12" xl="4">
          <template>
            <b-button
              :disabled="answerOptions.length <= 0"
              @click="goToNext"
              variant="primary"
              >{{ $t("NEXT") }}</b-button
            >
          </template>
          <template v-if="!questions.first_question_flg">
            <b-button @click="goToBack" variant="primary" class="ml-4">{{
              $t("BACK")
            }}</b-button>
          </template>
        </b-col>
      </b-row>
      <br />
    </div>
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import ShowErrors from "@/components/ShowErrors";
import { required } from "vuelidate/lib/validators";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "JudgementChart",
  components: { ShowErrors },
  data() {
    return {
      answerOptions: [],
      pageLoading: false,
      sessionAnswers: {},
      questionNo: "1",
      previousQuestionNo: "",
      previousQuestionArray: [],
      sessionQuestionAnswers: [],
      nextQuestionNo: "",
      questions: {
        question_no: "",
        first_question_flg: true,
        question_contents: "",
        question_kbn: "02",
        subsidy_type: "",
      },
    };
  },
  validations: {
    sessionAnswers: { required },
  },
  mounted() {
    this.getQuestions();
  },
  methods: {
    /**Displaying Question and Listing out options */
    async getQuestions() {
      this.sessionAnswers = {};
      this.$v.$reset();
      this.pageLoading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUBSIDYJUDGEMENTQUESTIONS,
        {
          question_no: this.questionNo,
        }
      );
      this.pageLoading = false;
      if (data.success) {
        let responseData = data.data;
        let question = responseData.questions[0];
        this.questions = {
          first_question_flg: question.first_question_flg
            ? question.first_question_flg
            : false,
          question_contents: question.question_contents
            ? question.question_contents
            : "",
          question_kbn: question.question_kbn ? question.question_kbn : "",
          question_no: question.question_no ? question.question_no : "",
          subsidy_type: question.subsidy_type ? question.subsidy_type : "",
        };
        let answers = responseData.answers;
        let answerData = [];
        answers.forEach((item) => {
          let answer = {
            value: {
              answerNo: item.answer_no ? item.answer_no : "",
              finalFlg: item.final_flg ? item.final_flg : "",
              judgePoint: item.judge_point != null ? item.judge_point : "",
              nextQuestionNo: item.next_question_no
                ? item.next_question_no
                : "",
              questionContents: item.question_contents
                ? item.question_contents
                : "",
              questionNo: item.question_no ? item.question_no : "",
              resultNo: item.result_no ? item.result_no : "",
              subsidyType: item.subsidy_type ? item.subsidy_type : "",
            },
            text: item.question_contents ? item.question_contents : "",
          };
          answerData.push(answer);
        });
        this.answerOptions = answerData;
      }
    },

    /**Submit answer for one question and go for next question and save answered data in store */
    goToNext() {
      this.$v.sessionAnswers.$touch();
      if (!this.$v.sessionAnswers.$invalid) {
        let selectedQA = {
          id: this.sessionAnswers.questionNo,
          question: this.questions,
          answer: this.sessionAnswers,
        };
        this.sessionQuestionAnswers.push(selectedQA);
        if (this.sessionAnswers.finalFlg) {
          this.$store.dispatch("updateSessionQA", this.sessionQuestionAnswers);
          this.$router.push({
            name: "subsidy_grant_judgement_content_confirmation",
          });
        } else {
          this.previousQuestionArray.push(this.sessionAnswers.questionNo);
          this.nextQuestionNo = this.sessionAnswers.nextQuestionNo;
          this.previousQuestionNo = this.sessionAnswers.questionNo;
          this.questionNo = this.nextQuestionNo;
          this.getQuestions();
        }
      }
    },

    /**Function for going back to previous question and remove the answered data in store for last question. */
    goToBack() {
      this.sessionQuestionAnswers.pop();
      this.questionNo = this.previousQuestionArray[
        this.previousQuestionArray.length - 1
      ];
      this.previousQuestionArray.pop();
      this.getQuestions();
    },

    /**Function to go for confirmation preview screen for all submitted answer  */
    goToJudgementResult() {
      this.$router.push({ name: "subsidyGrantJudgement_result" });
    },
  },
};
</script>

<style lang="scss">
$shadow-color: #dbd6d6;
.lable-btnAny {
  min-width: 10px !important;
  border-radius: 0px;
  cursor: default !important;
}

.errorPadding {
  padding-left: 20px;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  padding-top: 5px;
  font-size: 15px;
}
#judgement-group-expense .custom-control-label {
  border: 0.125rem solid #ffffff;
  border-radius: 3px;
  margin: 4px;
  padding: 0px 8px 0px 30px;
  min-width: 100px;
  box-shadow: 0 0 10px $shadow-color;
}
#judgement-group-expense .custom-control-label::before {
  border-radius: 50% !important;
}

#judgement-group-expense
  .custom-radio
  .custom-control-input:checked
  ~ .custom-control-label {
  border: 0.125rem solid #033170;
}

#judgement-group-expense
  .custom-radio.b-custom-control-sm
  .custom-control-label::before,
#judgement-group-expense
  .input-group-sm
  .custom-radio
  .custom-control-label::before {
  left: 0.5875rem;
}

#judgement-group-expense
  .custom-radio.b-custom-control-sm
  .custom-control-label::after,
#judgement-group-expense
  .input-group-sm
  .custom-radio
  .custom-control-label::after {
  left: 0.6rem;
}
#judgement-group-expense .custom-control-inline {
  padding: 0px !important;
  margin-right: 5rem !important;
  margin-bottom: 2rem !important;
}
#judgement-group-expense .custom-control {
  margin-right: 0rem !important;
}
#judgement-group-expense
  .custom-radio
  .custom-control-input:checked
  ~ .custom-control-label::after {
  left: 0.6rem;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e") !important;
}
.optionClass {
  text-align-last: left !important;
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 30px #00000014;
  border-radius: 50px !important;
  opacity: 1;
}
@media (max-width: 992px) {
  .ml-5 {
    margin-left: 0px !important;
  }
}
</style>